import { Component, OnInit } from '@angular/core';

import { BroadcastService } from '../../../../api/services/broadcast.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { UserService } from '../../../../api/services/user.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import {concatMap} from "rxjs/operators";
import * as _ from 'underscore';

@Component({
  selector: 'app-broadcast-history',
  templateUrl: './broadcast-history.component.html',
  styleUrls: ['./broadcast-history.component.scss']
})
export class BroadcastHistoryComponent implements OnInit {


  allBroadcast:any;
  allgroup:any;
  allCategory:any;
  allType:any;
  allUser:any;
  isDataLoaded:boolean = false;
  broadCastMessages:any;
  bcData:string = "";

  unitFieldType:string = "broadcastOn";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;
  totalFilterItems:any;

  constructor(
    public broadcastService: BroadcastService,
    private sharedService:SharedService,
    public lookupService: LookupService,
    public userService: UserService,
    private cookieService:CookieService
    ) { }

    getIndexParams(event){
      this.ItemStartIndex = event.ItemStartIndex;
      this.ItemEndIndex = event.ItemEndIndex;
    }
  
    sortUnitData(type){
      this.unitFieldType = type;
      this.unitOrder = !this.unitOrder;
    }
  
    getFieldOrderBy(type) {
      if(this.unitFieldType == type){
        return this.unitOrder ? 'asc' : 'desc';
      }
      else return '';
    }
  
    isMobileView() {
      return window.innerWidth <= 767 ? 'table-responsive' : '';
    }

  ngOnInit() {
    //this.getAllUser();
   // this.getAllGroup();
    this.getAllCategory();
    this.getAllType();
    
    this.getAllBroadcastMessage();
  }
  

  //Get All Group
  getAllGroup(){
    this.broadcastService.getAllBroadcastGroups().subscribe((res:any) => {
      this.allgroup = res;
    })
  }

  //Get All Category
  getAllCategory(){
    let apartmentID = parseInt(this.cookieService.get('apartmentId'));
    this.broadcastService.getBroadCastMessageCategories(apartmentID).subscribe((res:any) => {
      this.allCategory = res;
    })
  }

  

  //Get All Type
  getAllType(){
    this.lookupService.getLookupValueByLookupTypeId(11).subscribe((res:any) => {
      this.allType = res;
    })
  }
  

  // getGroupName(name){
  //   var data = _.filter( this.allgroup, function( item ) {
  //     if(item.broadcastGroupId === name)
  //       return item;
  //     } );

  //     if(data === undefined || data === null || data.length == 0){
  //       return '';
  //     }
  //     else {
  //       return data[0].groupName;
  //     }
  // }

  getCategoryName(name){
    var data = _.filter( this.allCategory, function( item ) {
      if(item.broadCastMessageCategoryId === name)
        return item;
      } );

      if(data === undefined || data === null || data.length == 0){
        return '';
      }
      else {
        return data[0].broadCastMessageCategory1;
      }
  }

  getTypeName(name){
    var data = _.filter( this.allType, function( item ) {
      if(item.lookupValueId === name)
        return item;
      } );

      if(data === undefined || data === null || data.length == 0){
        return '';
      }
      else {
        return data[0].lookupValueName;
      }
  }

  getUserName(name){
    var data = _.filter( this.allUser, function( item ) {
      if(item.userId === name)
        return item;
      } );

      if(data === undefined || data === null || data.length == 0){
        return '';
      }
      else {
        return data[0].firstName;
      }
  }
  getBroadCastMode(modeIds){
  
   if(modeIds.length>0)
   {
    var broadcastModeString="";
 var modeIdsArr=modeIds.split(',');
 if(modeIdsArr.length>0){
  modeIdsArr.forEach(element => {
    if(element=="40")
    {
      broadcastModeString=broadcastModeString+"General SMS,";
    }
    if(element=="41")
    {
      broadcastModeString=broadcastModeString+"Whatsapp,";
    }
    if(element=="42")
    {
      broadcastModeString=broadcastModeString+"Email,";
    }
   });
  return broadcastModeString=broadcastModeString.substring(0,broadcastModeString.length-1);
 }

   }
  }
  
  getAllBroadcastMessage(){
   let apartmentId= parseInt(this.cookieService.get('apartmentId'));
   var users$=this.userService.getAllUsers();
 var allBroadCastmessages$= users$.pipe(
    concatMap((users:any) =>{
      this.allUser = users;
      return  this.broadcastService.getAllBroadcastMessages(apartmentId);
    } )
   );
   

    allBroadCastmessages$.subscribe(
                (res:any)=>{
                    this.broadCastMessages=res;
                    this.broadCastMessages.forEach(element => {
                      element.CategoryName=this.getCategoryName(element.broadcastMessageCategoryId);
                      element.BroadCastMode=this.getBroadCastMode(element.broadcastModeIds);
                      element.UserName=this.getUserName(element.broadcastBy);
                    });
                    this.isDataLoaded = true;
                     this.totalItems = this.broadCastMessages.length;
                      if(this.totalItems>this.itemLimit){
                        this.ItemEndIndex = this.itemLimit;
                      }
                      else {
                        this.ItemEndIndex = this.totalItems;
                      }
                }

    );
    
  }

}
